import $ from 'jquery'

$.fn.cookieBar = function(options) {
  const settings = $.extend({
    'acceptButton': '.js-cookieBarAccept',
    'declineButton': '.js-cookieBarDecline',
    'secure': false,
    'path': '/',
    'domain': '',
    'threshold': 500
  }, options)

  const _hide = ($cookiebar) => {
    $cookiebar
      .attr('aria-hidden', 'true')
      .attr('aria-live', 'off')
      .hide()
  }

  const _show = ($cookiebar) => {
    $cookiebar
      .attr('aria-hidden', 'false')
      .attr('aria-live', 'polite')
      .show()
  }

  const _accept = function($cookiebar) {
    // $.cookie('cookiebar', 'hide', {
    $.cookie('cookies_consent', 'true', {
      path: settings.path,
      secure: settings.secure,
      domain: settings.domain,
      expires: 30
    })
    $(document).trigger('accept.cookiebar', [$cookiebar])
  }

  const _decline = function($cookiebar) {
    $.cookie('cookies_consent', 'false', {
      path: settings.path,
      secure: settings.secure,
      domain: settings.domain,
      expires: 30
    })
    $(document).trigger('decline.cookiebar', [$cookiebar])
  }

  $(document).on('accept.cookiebar', (e, $cookiebar) => {
    _hide($cookiebar)
  })

  $(document).on('decline.cookiebar', (e, $cookiebar) => {
    _hide($cookiebar)
  })

  $.cookieBar = $.cookieBar || {}

  /* $.cookieBar.isAccepted = function() {
    return $.cookie('cookiebar') === 'hide'
  } */

  $.cookieBar.isChosen = function() {
    return $.cookie('cookies_consent') === 'true' || $.cookie('cookies_consent') === 'false'
  }

  return this.each(function() {
    var $cookiebar = $(this)

    // if (!$.cookieBar.isAccepted()) {
    if (!$.cookieBar.isChosen()) {
      if (settings.threshold > 0) {
        $(window).on('scroll.cookiebar', (function() {
          if ($(window).scrollTop() > settings.threshold) {
            // $(window).unbind('scroll.cookiebar')
            // _accept($cookiebar)
            _hide($cookiebar)
          }
        }))
      }
      _show($cookiebar)
    }

    $cookiebar
      .find(settings.acceptButton)
      .click(function() {
        _accept($cookiebar)
        return false
      })

    $cookiebar
      .find(settings.declineButton)
      .click(function() {
        _decline($cookiebar)
        return false
      })
  })

}



